.Input-element {
  /* background-color: yellow; */
  -webkit-animation: grow 0.4s ease-in-out forwards;
          animation: grow 0.4s ease-in-out forwards;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  height: 80px;
  display: grid;
  grid-template-columns: 1fr auto;
  grid-row-gap: 200px;
  -ms-flex-align: center;
      align-items: center;
  box-sizing: border-box;
}

.Input-element label{
  font-size: 0.85em;
  color: grey;
}

.Input-element:focus-within label{
  color: var(--highlight-color);
}

.Input-element input[type=text] {
  font-size: 1.0em; /* google chrome needs this*/
  height: 1.85em;
  border:none;
  border-bottom: 1px solid lightgrey;
  width: 100%;
  padding-top: 6px;
}

.Input-element input[type=text]:focus {
  outline: none; /* google chrome needs this*/
  border-bottom: 2px solid var(--highlight-color);
  padding-top: 5px;
}

.Input-element button {
  color: darkgrey;
  font-size: 2.1em;
  font-weight: 100;
  text-transform: uppercase;
  background-color: white;
  border: none;
  width: 50px;
  height: 50px;
}

@-webkit-keyframes grow {
  from {
    opacity: 0.5;
  }
  to {
    opacity: 1;
  }
}

@keyframes grow {
  from {
    opacity: 0.5;
  }
  to {
    opacity: 1;
  }
}


